.login-wrapper {
  overflow: visible;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99; }

.input-wrapper {
  position: relative;
  width: 100%;
  margin-bottom: 5em; }

.login-panel {
  flex: 1;
  max-width: 550px;
  padding: 10px 20px;
  background: #fefefe;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  position: relative; }
  .login-panel .title {
    text-align: center;
    -webkit-animation-delay: 0.23s;
    -moz-animation-delay: 0.23s;
    -o-animation-delay: 0.23s;
    animation-delay: 0.23s;
    font-weight: 300; }
  .login-panel .input--login:nth-of-type(1) {
